import React, { useState } from "react";
import InfosDataService from "../services/InfosService";

const InfosConducteur = () => {
  const initialInfosState = {
    id: null,
    refVehicule: "",
    marqueVehicule: "",
    assure: "", ageConducteur:"", sexeConducteur:"", etatConducteur:"", 
    professionConducteur:""
  };
  const [infos, setInfos] = useState(initialInfosState);
  const [submitted, setSubmitted] = useState(false);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setInfos({ ...infos, [name]: value });
  };

  const saveInfos = () => {
    var data = {
      refVehicule: infos.refVehicule,
      marqueVehicule: infos.marqueVehicule,
      assure:infos.assure, ageConducteur:infos.ageConducteur, 
      sexeConducteur:infos.sexeConducteur, etatConducteur:infos.etatConducteur, 
      professionConducteur:infos.professionConducteur
    };

    InfosDataService.create(data)
      .then(response => {
        /*setPv({
          id: response.data.id,
          title: response.data.title,
          description: response.data.description,
          published: response.data.published
        });*/
        setSubmitted(true);
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  const newInfos = () => {
    /*setPv(initialPvState);
    setSubmitted(false);*/
  };

  return (
    <div>
    <div className="submit-form">
      <h4>Informations sur les vehicules et conducteurs</h4>
      {submitted ? (
        <div>
          <h4>You submitted successfully!</h4>
          <button className="btn btn-success" onClick={newInfos}>
            Add
          </button>
        </div>
      ) : (
        <div className="m-2">
          <hr/>
          <div className="form-group">
                <h6 htmlFor="refVehicule">Référence vehicule</h6>
                <div className='input-group'>
                  <input type="text" className="form-control" id="refVehicule" required 
                  value={infos.refVehicule} onChange={handleInputChange} name="refVehicule"/>
                </div>
          </div>
          <hr/>
          <div className="form-group">
            <h6>Marque</h6>
            <div className='input-group'>
                  <input type="text" className="form-control" id="marqueVehicule" required 
                  value={infos.marqueVehicule} onChange={handleInputChange} name="marqueVehicule"/>
            </div>
          </div>
          <hr/>
          <div className="form-group">
            <h6>Assuré</h6>
            <select className="form-control" id="assure" required 
                value={infos.assure} onChange={handleInputChange} name="assure">
                <option>Non</option>
                <option>Oui</option>
            </select>
          </div>
          <hr/>
          <div className="form-group">
            <h6 htmlFor="ageConducteur">Age conducteur</h6>
            <input type="number" className="form-control" id="ageConducteur" required
              value={infos.ageConducteur} onChange={handleInputChange} name="ageConducteur" />
          </div>
          <hr/>
          <div className="form-group">
            <h6 htmlFor="sexeConducteur">Sexe conducteur</h6>
            <select className="form-control" id="sexeConducteur" required 
                value={infos.sexeConducteur} onChange={handleInputChange} name="sexeConducteur">
                <option>Feminin</option>
                <option>Masculin</option>
            </select>
          </div>
          <hr/>
          <div className="form-group">
            <h6 htmlFor="professionConducteur">Profession conducteur</h6>
            <input type="text" className="form-control" id="professionConducteur"
              required value={infos.professionConducteur} onChange={handleInputChange}
              name="professionConducteur"/>
          </div>
          <hr/>
          <div className="form-group">
            <h6 htmlFor="etatConducteur">Etat conducteur</h6>
            <select className="form-control" id="etatConducteur" required 
                value={infos.etatConducteur} onChange={handleInputChange} name="etatConducteur">
                <option>Blessé</option>
                <option>Décédé</option>
                <option>Aucun impact</option>
            </select>
          </div>
          <hr/>
          <button onClick={saveInfos} className="btn btn-success">
            Enregistrer
          </button>
        </div>
      )}
    </div>
    </div>
  );
};

export default InfosConducteur;
