import http from "../http-common";

const getAll = () => {
  return http.get("/infos");
};

const get = id => {
  return http.get(`/infos/${id}`);
};

const create = data => {
  return http.post("/infos", data);
};

const update = (id, data) => {
  return http.put(`/infos/${id}`, data);
};

const remove = id => {
  return http.delete(`/infos/${id}`);
};

const removeAll = () => {
  return http.delete(`/infos`);
};

const findByTitle = title => {
  return http.get(`/infos?title=${title}`);
};

const InfosService = {
  getAll,
  get,
  create,
  update,
  remove,
  removeAll,
  findByTitle
};

export default InfosService;
